import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import DataCuration from "../views/DataCuration.vue";
import GetHelp from "../views/GetHelp.vue";
import DepositData from "../views/DepositData.vue";
import CiteData from "../views/CiteData.vue";
import DataManagement from "../views/DataManagement.vue";
import Workshop from "../views/Workshop.vue";
import About from "../views/About.vue";
import Mission from "../views/Mission.vue";
import Team from "../views/Team.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },

  {
    path: "/data-curation",
    name: "DataCuration",
    component: DataCuration,
  },

  {
    path: "/team",
    name: "Team",
    component: Team,
  },

  {
    path: "/mission",
    name: "Mission",
    component: Mission,
  },

  {
    path: "/about",
    name: "About",
    component: About,
  },

  {
    path: "/workshop",
    name: "Workshop",
    component: Workshop,
  },


  {
    path: "/msc-data-management",
    name: "DataManagement",
    component: DataManagement,
  },

  {
    path: "/cite-data",
    name: "CiteData",
    component: CiteData,
  },

  {
    path: "/deposit-data",
    name: "DepositData",
    component: DepositData,
  },

  {
    path: "/get-help",
    name: "GetHelp",
    component: GetHelp,
  },

  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue"),
  },
];

const router = new VueRouter({
  routes,
});

export default router;
