       <section>
        <p><u>Citing Data Used</u></p>
        <h5>
            A research that analyses statistical data would need
            to cite the primary data sources in the final published papers,
            just as they would cite other sources. 
            The essence of the citation is to ensure that, the research data can be:<br><br>
            <ul>
                <li> a.detected</li>
                <li> b.reused</li>
                <li> c.replicated for validation purposes</li>
                <li> d.credited for acknowledgement</li>
                <li> e.tracked to measure usage and impact</li>
            </ul>
        </h5><br>

        <p><u>How to Cite Data</u></p>
        <h5>
            <p>Citing data is quite simple. Each citation must include the fundamental 
            elements that allow for a unique dataset to be identified over time:</p><br>
            <ul>
                <li>a. Author</li>
                <li>b. Title</li>
                <li>c. Version</li>
                <li>d. Date</li>
                <li>e. Persistent identifier (e.g. Digital Object Identifier)</li>
            </ul>
        </h5>
      </section>
