          <div class="card-content">
            <div class="content">
              Researchers who wish to submit their data with other researchers, 
              or as part of requirement by funders of the research can submit it
              via <a href="mailto: c-damaa@ucc.edu.gh">c-damaa@ucc.edu.gh</a>.
              Sharing data with C-DAMAA would enhance the visibility of your research 
              and increase the number of citations of that particular research:<br><br>

              a. Does C-DAMAA charge me to deposit my data with their Data Service?<br>
              There is no fee charged by C-DAMAA on deposits of datasets by researchers for public use.
               The Centre, however, provide some other services for which we charge some fees, 
               including:
               <ul>
               <li> Data cleaning</li>
               <li> Creation of user documents, e.g. user manuals, analytic notes etc.</li>
               <li> Provision of regular reports on how data is been used.</li>
               </ul><br>

               b. Type of Datasets and formats accepted by C-DAMAA<br>
               C-DAMAA accepts administrative records, survey research data, 
               and any other data that could be useful for research. 
               C-DAMAA accepts data files in all formats, however, 
               the centre would convert the datasets into standard formats.<br><br>

               c. What should be attached to the datafiles?<br>
               Researchers who submit data to C-DAMAA must include background 
               documentation help other users of the data to understand and 
               appreciate the data. This should include administrative forms, 
               questionnaires, code lists, manuals, enumerator manuals, and reports.

