    <!--overview-->
    <div class="container">
      <p>Data Curation</p>
      <hr />
      <br />
      <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title">a. Overiew</h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.open ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
              C-DAMAA provides the platform for the submission and storage of
              datasets. The Centre also provides access to these datasets for
              reuse by other researchers. Data curation is the process C-DAMAA
              employs to add value to data, maximise access and enhance the
              long-term usage and preservation of the data. The data curation
              process also ensures that datasets are cleaned, well organised,
              described and preserved for public use. In today’s world, it is
              relatively easy for data to be shared, however, without any proper
              data curation it would be relatively difficult for such datasets
              to be found, use and properly interpreted. Through data curation,
              C-DAMAA provides lasting and meaningful access to data .
            </div>
          </div>
        </b-collapse>
      </section>
      <br />
      <!--end of overview-->

      <!--submission of data-->
      <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title">b. Submission of data</h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.open ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
              C-DAMAA accepts data from census or surveys and other
              administrative records. C-DAMAA accepts data files in all
              proprietary formats (e.g. Excel, SPSS, Eviews, STATA etc). All
              datasets that are been submitted to the Centre must come with all
              necessary documentation. The documentation should include among
              others the questionnaires (if any), codebooks and reports. Please,
              it is important for researchers who submit datasets to C-DAMAA to
              ensure that they have the rights to share the data with the
              Centre.
            </div>
          </div>
        </b-collapse>
      </section>
      <br />
      <!--end of submission of data-->

      <!--begining of data assuarance-->
      <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title">c. Data Assuarance</h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.open ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
              <ul>
                <li><u>Disclosure control</u></li>
                To ensure the anonymity of respondents for each dataset
                submitted, the Centre undertakes disclosure control in order to
                ensure that data files do not contain identifiable information
                of respondents.
                <a href="../assets/flowchart.png"
                  >View the C-DAMAA disclosure control flowchart</a
                >
                .

                <li><u>Data Quality Check</u></li>
                Dataset submitted to C-DAMAA is usually taken through quality
                control to ensure the accuracy and usability of data submitted
                to the Centre. Data management experts and data curators within
                the Centre check for anomalies in the data file and
                documentation associated with the data and correct them in
                consultation with data depositors.
              </ul>
            </div>
          </div>
        </b-collapse>
      </section>
      <br />
      <!--end of data assuarance-->

      <!--begining of metadata creation-->
      <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title">d. metadata creation</h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.open ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
              All datasets provided by C-DAMAA comes with extensive user
              information created for each specific datasets. The metadata for
              each dataset is produced according to the
              <a href="https://ddialliance.org/"
                >Data Documentation Initiative</a
              >
              data description standard. The Centre also uses the metadata
              creation template provided by
              <a href="https://www.nesstar.com/">NESSTAR</a>.
            </div>
          </div>
        </b-collapse>
      </section>
      <br />

      <!--begining of archiving data-->
      <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title">e. archiving data</h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.open ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
              An archival version of all iterations of each dataset is
              maintained by C-DAMAA. Archival versions are securely kept and
              migrated as technology changes, to ensure they are always
              accessible.
            </div>
          </div>
        </b-collapse>
      </section>
      <!--end of archiving data-->
