    <div class="container">
      <p>Msc. Data Management and Analysis</p><hr>
      <h2>C-DAMAA in collaboration with its mother department 
      (Dept. of Data Science and Economic Policy) of the 
      UCC School of Economics offers a two-year (Sandwich) 
      Master of Science Programme in Social Science Data Management and Analysis. 
      The broad objective of the programme is to provide knowledge on data management
      approaches and hands-on analytical skills to enhance an incremental use of data in
       teaching, research and policymaking. The specific objectives are to:
      </h2>
       <ul>
            <li> a. Build the capacity of students in micro-data management</li>
            <li> b. Equip students with the requisite skills for writing data management plans</li>
            <li> c. Enable students to apply advanced research skills in the production of ideas</li>
            <li> d. Expose students to the emerging wide range of computer-based tools for data management and analysis.</li>
       </ul><br>
       <p><u>Structure of the program</u></p>
       <h2>
       The MSc. Social Science Data Management and Analysis comprises six 
       compulsory courses in the first Sandwich semester and four compulsory 
       courses in the second sandwich semester. Students would have the option 
       of selecting one additional optional course from a pool of three optional courses. 
       The programme comprises of 330 credit hours.
       </h2><br>

       <p>a. FIRST SEMESTER COURSES (18 CREDIT HOURS):</p><br>

       <!--first semester-->
       <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title"> Philosophy Of Research</h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.open ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
            <p>DMA 801S: Philosophy of Research. 3 CREDITS</p><hr>
            <p>
            This course traces the development of various philosophical 
            perspectives of knowledge, sources of knowledge and schools of 
            thought on knowledge generation and research.
            </p>
            <p><u>Course Objectives:</u></p>
            <h5>Students should be able to:</h5>
            <ul>
              <li>Explain Western, African and Asian philosophical 
              thoughts that have shaped knowledge generation and research
              </li>
              <li>Appreciate the distinction between reality and different
               perspectives and thus understand that different people 
               will define issues in different ways.</li>
            </ul>
            <p><u>Course Content</u></p>
            <p>Among the issues to be discussed are: Nature of science: 
            Theory of science, the scientific enterprise; 
            Theories of knowledge and ideas, ontology and methodology; 
            Selected philosophers in Western and African thought:
             e.g. St. Augustine; Plato; John S. Mill, John Locke,
              Hume, Kant, Berkeley, Wiredu, Gyekye; Confucius; 
              Schools of thought which have influenced social science 
              research: e.g. positivism, phenomenology, hermeneutics, 
              existentialism, feminist epistemology, epicurean thought, 
              Marxist thought, cosmological and ontological arguments, 
              justice; selected Eastern (Indo-Chinese) thought; Africa cosmology 
              of life; and ethics in research.</p>
              <p><u>Main Text</u></p>
              <ul>
                <li>Matthews, M. R. (2014). International 
                Handbook of Research in History,
                 Philosophy and Science Teaching. Springer</li>
              </ul>
              <p><u>Suggested Reading List</u></p>
              <ul>
                <li>Babbie, E. (2012). The practice of social research. Cengage Learning.</li>
                <li>Geddes, B. (2003). Paradigms and Sand Castles: 
                Theory Building and Research Design in Comparative Politics. 
                University of Michigan Press.</li>
                <li>Hughes, J. A. & Sharrock, W. W. (1980). 
                The philosophy of social research. London: Longman.</li>
              </ul>
              <br>

            </div>
          </div>
        </b-collapse>
      </section>
      <!--end of first semester-->
    <br>

    <!--second semester-->
    <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title"> Data Literacy and Mining</h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.open ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
              <p>DMA 803S: Data Literacy and Mining. 3 CREDITS</p><hr>
            <p>
            This course is primarily premised on the fact that the 
            structure of a dataset, broad patterns and trends and 
            other ancillary characteristics provide an indication 
            of the appropriate analytical tools that will be suitable.
             Data literacy and mining is incremental gaining prominence
              due to the realised need to replicate scientific findings.
            </p>
            <p><u>Course Objectives:</u></p>
            <h5>Students should be able to:</h5>
            <ul>
              <li>Gain insight into the challenges and limitations 
              of different data mining techniques.
              </li>
              <li>Apply data literacy and mining solutions using common
               data mining software tool (e.g. WEKA, SPSS, Data Miner, etc)</li>
               <li>Prepare data for different types of analyses</li>
            </ul>
            <p><u>Course Content</u></p>
            <p>Among the issues to be considered are interpreting 
            data at different stages of the analytical process, 
            data warehousing, handling outliers and missing values 
            and engaging with differences that emerge between initial
             descriptive outcomes and inferential analysis.</p>
              <p><u>Main Text</u></p>
              <ul>
                <li>Corti, L., Van den Eynden, V., Bishop, L., & Woollard, M. (2014). 
                Managing and Sharing Research Data: A Guide to Good Practice. Sage.</li>
              </ul>
              <p><u>Suggested Reading List</u></p>
              <ul>
                <li>Ng, A. &‎ Soo, K. (2017). Numsense! Data Science for the Layman. Kindle.</li>
                <li>Prodromou, T. (2017). Data Visualization and Statistical 
                Literacy for Open and Big Data (Advances in Data Mining and Database Management). 
                IGI Global</li>
              
              </ul><br>

            </div>
          </div>
        </b-collapse>
      </section><br>
      <!--end of second semester-->

    <!--optional courses-->
       <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title">Survey and Census Methodology</h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.open ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
                <p>DMA 805S: Survey and Census Methodology. 3 CREDITS</p><hr>
            <p>
            Engagement in surveys and censuses remain the traditional
             modes of collecting data for further investigation. 
             Skills and contextual experiences in using these modes
              of data collection will be discussed in this course.
            </p>
            <p><u>Course Objectives:</u></p>
            <h5>Students should be able to:</h5>
            <ul>
              <li>Distinguish between the different modes of data collection,
               their relative merits and demerits and the needed skills and mind set for each of them
              </li>
              <li>Explain the rudiments of answering research hypotheses</li>
               <li>Catalogue data quality procedures during the conduct of survey and censuses</li>
            </ul>
            <p><u>Course Content</u></p>
            <p>The course will cover the fundamentals of the survey lifecycle.
             Topics covered include: questionnaire design, sampling design, 
             data collection methods, data analysis, and quality checks from
              the total survey error perspectives which are inclusive of measurement,
               non-response, and coverage errors. This will include challenges 
               faced in translation and adaptation of questionnaires, 
               implementing proper sampling techniques, quality assurance in
                multi-national studies, and cultural variations in response styles. 
                Various strategies to deal with typically encountered challenges will be addressed.</p>
              <p><u>Main Text</u></p>
              <ul>
                <li>Wolf, C., Joye, D., Smith, T. W & Fu, Yang-chih. (2016). 
                The SAGE Handbook of Survey Methodology. Sage</li>
              </ul>
              <p><u>Suggested Reading List</u></p>
              <ul>
                <li>Bass, F. (2013). Guide to the Census. Bloomberg Press</li>
                <li>Swanson, D. A. & Tayman J. (2012). Subnational 
                Population Estimates. Springer</li>
              
              </ul><br>

            </div>
          </div>
        </b-collapse>
      </section>
      <!--end optional courses-->
      <br>

      <!-- ethics-->
      <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title">Ethics and Disclosure Research</h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.close ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
                <p>DMA 807S: Ethics and Disclosure Research. 3 CREDITS</p><hr>
            <p>
            Issues on ethical considerations enjoin researchers to be
             responsible in their engagement as knowledge providers. 
             Ethical vetting and adherence to scientific research guidelines 
             have received increased attention among funders of research
              and publishers of research articles making them inevitable
               in managing and analyzing data.
            </p>
            <p><u>Course Objectives:</u></p>
            <h5>Students should be able to:</h5>
            <ul>
              <li>Apply theories and methods in ethics and research ethic
              </li>
              <li>Reflect on oral dilemmas for researchers and Research Ethics Committees, and 
              be able to systematically analyze such dilemmas</li>
               <li>Assess the various arguments involved and work towards 
               a well-reasoned position or decision in research.</li>
            </ul>
            <p><u>Course Content</u></p>
            <p>The course deals with issues associated with professional 
            ethics concerning research, understanding of ethical concerns 
            that may arise from research activities, especially those 
            involving human subjects; essence of concepts such as 
            information disclosure, consent of participants, privacy, 
            anonymity and confidentiality. Students will also gain better
             appreciation of the methodologies and research involving 
             special populations and issues of confidentiality.</p>
              <p><u>Main Text</u></p>
              <ul>
                <li>Corti, L., Van den Eynden, V., Bishop, L., & Woollard, M. (2014).
                 Managing and Sharing Research Data: A Guide to Good Practice. Sage.</li>
              </ul>
              <p><u>Suggested Reading List</u></p>
              <ul>
                <li>American Statistical Association (1999, August 7). 
                “Ethical Guidelines for Statistical Practice.” 
                Prepared by the Committee on Professional Ethics,
                 Approved by the Board of Directors. <a href="https://www.amstat.org/about/ethicalguidelines">https://www.amstat.org/about/ethicalguidelines</a>.
                  cfm (accessed Nov. 15, 2011)</li>
                <li>Berg, B. L. & Lune, H. (2004).
                 Qualitative research methods for the social sciences (Vol. 5). Boston: Pearson.</li>
              
              </ul><br>

            </div>
          </div>
        </b-collapse>
      </section>
      <!--end optional courses-->
      <br>

      <!-- ethics-->
      <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title">Computer Applications for Analytical Research</h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.close ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
                <p>DMA 809S: Computer Applications for Analytical Research. 3 CREDITS</p><hr>
            <p>
            Processing large datasets to inform policy underscores
             the orientation of this course. Literacy in computer 
             soft wares greatly influences research productivity and quality..
            </p>
            <p><u>Course Objectives:</u></p>
            <h5>Students should be able to:</h5>
            <ul>
              <li>Use different computer soft wares to generate knowledge
              </li>
              <li>Produce visual information to enhance impactful communication of research findings</li>
               <li>Appreciate best practices needed in the use of computer softwares.</li>
            </ul>
            <p><u>Course Content</u></p>
            <p>In this course, students will be introduced to Microsoft Excel 
            as a tool for performing simple data analysis and manipulations.
             In addition, students will be exposed to other soft wares such as NVivo, STATA, SPSS,
              EViews and NUDist.</p>
              <p><u>Main Text</u></p>
              <ul>
                <li>Miller, D. C. & Salkind, N. J. (2002) Using the Computer in 
                Social Science Research In: Handbook of Research Design & Social Measurement. Sage</li>
              </ul>
              <p><u>Suggested Reading List</u></p>
              <ul>
                <li>Agung, G. N. (2008). Time Series Data Analysis Using EViews, Wiley.</li>
                <li>Bazeley, P. & Jackson K. (2013). Qualitative Data Analysis with NVivo.Sage Publications .</li>
                 <li>Cameron, A. C. & Trivedi, P. K. (2009). Microeconometrics 
                 using stata (Vol. 5). College Station, TX: Stata Press.</li>
              </ul><br>

            </div>
          </div>
        </b-collapse>
      </section><br>
      <!--end optional courses-->

      <!--research methods-->
      <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title">Mixed Research Methods</h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.close ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
                <p>DMA 811S: Mixed Research Methods. 3 CREDITS</p><hr>
            <p>
            Mixed Research Method focuses on the design and implementation 
            of research that blends qualitative and quantitative data collection and analysis.
             A key issue in the course is the debate about the ways to integrate
              qualitative and quantitative data.
            </p>
            <p><u>Course Objectives:</u></p>
            <h5>Students should be able to:</h5>
            <ul>
              <li>Identify the distinguishing features of major mixed method research designs.
              </li>
              <li>Produce visual information to enhance impactful communication of research findings</li>
               <li>Become conversant with a variety of ways that qualitative and
                quantitative data and analytical procedures can be mixed.</li>
            </ul>
            <p><u>Course Content</u></p>
            <p>The course would provide an overview of mixed methods 
            research, consisting of the history and philosophy of mixed methods,
             emerging literature within the field, purposes and characteristics
              of mixed methods research, types of research problems typically addressed,
               the specification of mixed methods purpose statements and research 
               questions and types of major mixed methods designs.
                A large emphasis of this course we will focus on the “why” 
                rather than the “how”, although we will discuss mixed methods designs 
                and how they are used in social science research.</p>
              <p><u>Main Text</u></p>
              <ul>
                <li>Freeman, M. (2016). Modes of Thinking for Qualitative Data Analysis. Routledge.</li>
              </ul>
              <p><u>Suggested Reading List</u></p>
              <ul>
                <li>Creswell, J. (2013). Qualitative inquiry and research design: 
                Choosing among five approaches (3rd ed.). Thousand Oaks, CA: Sage.</li>
                <li>Dey, I. (2003). Qualitative data analysis: 
                A user friendly guide for social scientists. Routledge.</li>
                 <li>Grbich, C. (2013). Qualitative data analysis:
                  An introduction (2nd ed.). London, UK: Sage.</li>
              </ul>

            </div>
          </div>
        </b-collapse>
      </section><br><br><br>
      <!--end of research-->

      <!--second semester-->
      <p>b. SECOND SEMESTER COURSES (15 CREDIT HOURS):</p><br>
      <!--end of second semester-->

      <!--Data Curation and Management Plans-->
      <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title">Data Curation and Management Plans</h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.close ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
                <p>DMA 802S: Data Curation and Management Plans. 3 CREDITS</p><hr>
            <p>
            This course builds on Data Literacy and Mining to
             improve data documentation and preparation for analyses. 
             It is intended to enhance proper storage and archiving 
             of data for organizations and to ensure increase in the reuse of data collected.
            </p>
            <p><u>Course Objectives:</u></p>
            <h5>Students should be able to:</h5>
            <ul>
              <li>Prepare data for archiving and preservation and
               institute strategies for promoting the reuse of data.
              </li>
              <li>Cure data and provide documentation of datasets</li>
               <li>Rescue abandoned data sets as a result of poor
                documentation and improper archiving.</li>
            </ul>
            <p><u>Course Content</u></p>
            <p>Topics to be covered in this course include data review processing, 
            curation and publication. In addition, essentials for preparing data 
            management plans either for personal research or as a means of meeting 
            funding requirements will be taught.</p>
              <p><u>Main Text</u></p>
              <ul>
                <li>Ray, J. M. (2014). Research Data Management: Practical Strategies
                 for Information Professionals. Purdue University Press.</li>
              </ul>
              <p><u>Suggested Reading List</u></p>
              <ul>
                <li>Getler, M. (2012). Managing Research Data. Facet Publishing</li>
                <li>Karasti, H., Baker, K. S. & Halkola, E. (2006).
                 Enriching the notion of data curation in e-science:
                  data managing and information infrastructuring in the long
                   term ecological research (LTER) network. Computer Supported
                    Cooperative Work (CSCW), 15(4), 321-358..</li>
              </ul>

            </div>
          </div>
        </b-collapse>
      </section><br>
      <!--end of Data Curation and Management Plans-->

      <!--qualitative data analysis-->
      <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title"> Qualitative Data Analysis </h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.close ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
                <p>DMA 804S: Qualitative Data Analysis. 3 CREDITS</p><hr>
            <p>
            This course introduces students to the field of qualitative data
             analysis for research and prepares them in the skills, techniques,
            and knowledge necessary to undertake independent research using this methodology..
            </p>
            <p><u>Course Objectives:</u></p>
            <h5>Students should be able to:</h5>
            <ul>
              <li>Appreciate the history and development of qualitative research methodology.
              </li>
              <li>Apply qualitative techniques in engaging in research</li>
               <li>Understand subjectivity, role, access, and ethics in qualitative research studies.</li>
            </ul>
            <p><u>Course Content</u></p>
            <p>The course examines the various philosophical assumptions 
            underlying qualitative research methods, their implication for 
            framing research problems/questions, data collection techniques, 
            analysis, conclusion and dissemination of findings of qualitative research..</p>
              <p><u>Main Text</u></p>
              <ul>
                <li>Ray, J. M. (2014). Research Data Management: Practical Strategies
                 for Information Professionals. Purdue University Press.</li>
              </ul>
              <p><u>Suggested Reading List</u></p>
              <ul>
                <li>Getler, M. (2012). Managing Research Data. Facet Publishing</li>
                <li>Karasti, H., Baker, K. S. & Halkola, E. (2006).
                 Enriching the notion of data curation in e-science:
                  data managing and information infrastructuring in the long
                   term ecological research (LTER) network. Computer Supported
                    Cooperative Work (CSCW), 15(4), 321-358..</li>
              </ul>

            </div>
          </div>
        </b-collapse>
      </section><br>
      <!--end of qualitative data-->


      <!--social network-->
      <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title"> Social Network and Analysis </h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.close ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
                <p>DMA 806S: Social Network and Analysis. 3 CREDITS</p><hr>
            <p>
            Social network analysis involves a mix of quantitative 
            and qualitative methods for describing, measuring and analysing
             social and spatial networks. Students will be taken through 
             the process of identifying influential individuals, track the spread
              of information, disease and ideas through networks, and how to apply these techniques.
            </p>
            <p><u>Course Objectives:</u></p>
            <h5>Students should be able to:</h5>
            <ul>
              <li>Explain the various concepts, methods, and 
              applications of social network analysis drawn from the social and behavioral sciences.
              </li>
              <li>Analyze relational data measured on groups of social actors.</li>
            </ul>
            <p><u>Course Content</u></p>
            <p>Topics to be discussed include an introduction to 
            graph theory and the use of directed graphs to study 
            structural theories of actor interrelations; structural and
             locational properties of actors, such as centrality, prestige, 
             and prominence; subgroups and cliques; equivalence of actors,
              including structural equivalence, block models, and an introduction to role algebras.</p>
              <p><u>Main Text</u></p>
              <ul>
                <li>Scott, J. (2017). Social Network Analysis, Fourth Edition. Sage Publishing.</li>
              </ul>
              <p><u>Suggested Reading List</u></p>
              <ul>
                <li>Borgatti, S. P., Everett, M. G., & Johnson, J. C. 
                (2013). Analysing Social Networks. Sage Publications</li>
                <li>Kadushin, C. (2011). Understanding Social Networks: 
                Theories, Concepts, and Findings. Oxford University Press</li>
              </ul>

            </div>
          </div>
        </b-collapse>
      </section><br>
      <!--end of social network-->

      <!--applied econometrics-->
      <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title"> Applied Econometrics </h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.close ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
                <p>DMA 806S: Applied Econometrics. 3 CREDITS</p><hr>
            <p>
            This is a hands-on course intended to provide practical 
            knowledge in estimating and interpreting empirical econometric models.
            It will introduce students to modern ways of evaluating theories using real data..
            </p>
            <p><u>Course Objectives:</u></p>
            <h5>Students should be able to:</h5>
            <ul>
              <li>Analyse datasets to respond to socio-economic challenges.
              </li>
              <li>Test relevance of theories.</li>
              <li>Assess impact of interventions and potential of different policy options.</li>
            </ul>
            <p><u>Course Content</u></p>
            <p>This course will provide a brief overview of econometric
             theory with emphasis on parametric analysis. 
             Specific issues to be discussed include model selection,
              transformations, heteroskedasticity, temporal and spatial 
              dependence, sample selection and endogeneity. 
              Others are binary response models, duration models and 
              quantile regression, and introductory treatment of nonparametric methods..</p>
              <p><u>Main Text</u></p>
              <ul>
                <li>Doran, J., Bourke, J. & Kirby, A. (2017).
                 Introduction to Applied Econometrics Analysis Using Stata. NuBooks.</li>
              </ul>
              <p><u>Suggested Reading List</u></p>
              <ul>
                <li>Asteriou, D. & Hall, S. G. (2015). 
                Applied Econometrics, Third Edition. Palgrave Macmillan.</li>
              <li>Wooldridge, J. (2012). Introductory econometrics: A modern approach. Cengage Learning.</li>
              <li>Wooldridge, J. M. (2010). Econometric analysis of
               cross section and panel data. MIT press.</li>

              </ul>

            </div>
          </div>
        </b-collapse>
      </section><br>
      <!--end of applied econometrics-->


      <!--dissertation-->
      <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title"> Dissertation  </h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.close ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
                <p>DMA 899S: Dissertation. 3 CREDITS</p><hr>
            <p>
           The Project offers an opportunity to put into practice
            skills that have been acquired over the period of study. 
            This will assess student’s ability to independently carry out an academic research assignment.
            </p>
            <p><u>Course Objectives:</u></p>
            <h5>Students should be able to:</h5>
            <ul>
              <li>Generate knowledge to address societal issues and advance the frontiers of knowledge.
              </li>
              <li>Contribute to academic discourses based on their 
              appreciation of scientific processes in knowledge generation.</li>
            </ul>
            </div>
          </div>
        </b-collapse>
      </section><br>
      <!--end of dissertation-->
      <p style="margin-left:20px;">Optional course (3 credit hours)</p><br><br>

      <!--optional courses-->
      <p>c. OPTIONAL COURSES(one to be selected)</p><br>

      <!--time series-->
      <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title"> Time Series Analysis  </h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.close ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
                <p>DMA 810S: Time Series Analysis . 3 CREDITS</p><hr>
            <p>
           Time series data is mainly used to analyse macroeconomic
            issues to help identify relationships and to predict/forecast outcomes. 
            As an elective course, the intent is to offer practical sessions 
            on how to handle such data using modern estimation techniques 
            and addressing issues related to fitness of different time series models.
            </p>
            <p><u>Course Objectives:</u></p>
            <h5>Students should be able to:</h5>
            <ul>
              <li>Estimate and test hypotheses using time series data.
              </li>
              <li>Choose appropriate test(s) to determine the estimation
               technique suitable for addressing a given research problem/objectives.</li>
               <li>Conduct time series post estimation analyses.
              </li>
            </ul>
            <p><u>Course Content</u></p>
            <p>The issues to be covered in this course are: understanding 
            time series properties of variables, univariate and stationarity
             of time series data, and multivariate time series analysis.</p>
              <p><u>Main Text</u></p>
              <ul>
                <li>Enders, W. (2014) Applied Econometric Time Series. 4th Edition Wiley.</li>
              </ul>
              <p><u>Suggested Reading List</u></p>
              <ul>
                <li>Agung, G. N. (2008). Time Series Data Analysis Using EViews, Wiley.</li>
              <li>Hamilton, J. D. (1994). Time Series Analysis. Princeton University Press.</li>
              <li>Lütkepohl, H. & Krätzig, M. (Eds.). (2004). 
              Applied time series econometrics. Cambridge University Press.</li>

              </ul>

            </div>
          </div>
        </b-collapse>
      </section><br>
      <!--end of time series-->

      <!--cross section-->
      <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title"> Cross Section Analysis  </h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.close ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
                <p>DMA 812S: Cross Section Analysis . 3 CREDITS</p><hr>
            <p>
           Cross sectional data provides ‘spot-check’ perspective
            of a phenomenon and thereby offers an opportunity 
            to generate an understanding of the context and to determine possible solutions.
             The course in cross sectional analysis will target analyses mainly at the micro level.
            </p>
            <p><u>Course Objectives:</u></p>
            <h5>Students should be able to:</h5>
            <ul>
              <li>Estimate and test hypotheses using cross sectional data.
              </li>
              <li>Address issues related to sample selection bias and endogeneity.</li>
               <li>Demonstrate practical knowledge in the handling of cross sectional data.
              </li>
            </ul>
            <p><u>Course Content</u></p>
            <p>The course will cover topics including; 
            data structure of cross sectional data and estimation concerns,
             directional and causal estimations, tobit and related censored 
             regression models, models dealing with sample selectivity, 
             treatment effects, propensity score matching and post estimation analyses.</p>
              <p><u>Main Text</u></p>
              <ul>
                <li>Anwar, N. & Najam, F. A. (2016). Structural Cross Sections: 
                Analysis and Design. Butterworth-Heinemann..</li>
              </ul>
              <p><u>Suggested Reading List</u></p>
              <ul>
                <li>Asteriou, D. & Hall, S. G. (2011). Applied econometrics. New York: Palgrave Macmillan.</li>
              <li>Cameron, A. C. & Trivedi, P. K. (2009). Microeconometrics using stata (Vol. 5). College Station, TX: Stata Press.</li>
              <li>Wooldridge, J. M. (2010). Econometric analysis of cross section and panel data. MIT press</li>

              </ul>

            </div>
          </div>
        </b-collapse>
      </section><br>
      <!--end of cross section-->

      <!--panel data-->
      <section>
        <b-collapse animation="slide" aria-id="contentIdForA11y3">
          <template #trigger="props">
            <div
              class="card-header"
              role="button"
              aria-controls="contentIdForA11y3"
            >
              <h2 class="card-header-title"> Panel Data Management and Analysis  </h2>
              <a class="card-header-icon">
                <img
                  src="../assets/drop.webp"
                  style="width: 20px; height: 20px"
                  v-bind="props.close ? 'menu-down' : 'menu-up'"
                />
              </a>
            </div>
          </template>

          <div class="card-content">
            <div class="content">
                <p>DMA 814S: Panel Data Management and Analysis. 3 CREDITS</p><hr>
            <p>
           Panel data management and analysis presents the 
           practical applications of theories and principles acquired
            in econometric theory in estimating panel data.
             This course has been drafted in recognition of the fact
              that the use of cross sectional and panel data has become a
               common practice among researchers and policymakers.
            </p>
            <p><u>Course Objectives:</u></p>
            <h5>Students should be able to:</h5>
            <ul>
              <li>Analyze variants of panel estimation techniques.
              </li>
              <li>Conduct panel post estimation tests to choose appropriate models.</li>
               <li>Interpret coefficients of panel estimation techniques.
              </li>
            </ul>
            <p><u>Course Content</u></p>
            <p>Topics to be taught include; fixed and random-effects model, 
            population-averaged models, probit , logit, poisson models for panel data,
             and panel data estimation under endogeneity. In addition, 
             models for slowing changing explanatory variables and dynamic models will be taught.</p>
              <p><u>Main Text</u></p>
              <ul>
                <li>Biørn, E. (2016). Econometrics of Panel Data: 
                Methods and Applications. Oxford University Press.</li>
              </ul>
              <p><u>Suggested Reading List</u></p>
              <ul>
                <li>Baltagi, B. (2008). Econometric analysis of panel data (Vol. 1). John Wiley & Sons.</li>
              <li>Cameron, A. C. & Trivedi, P. K. (2009). Microeconometrics using stata (Vol. 5). 
              College Station, TX: Stata Press.</li>
              <li>Wooldridge, J. M. (2010). Econometric analysis of cross section and panel data. MIT press.</li>

              </ul>

            </div>
          </div>
        </b-collapse>
      </section>
      <!--end of panel data-->
      <!--end of optional courses-->
      
      <br><br>

      <p><u>Admission Requirement:</u></p>
      <h3>
        For students to be enrolled should have a minimum of 
        first degree with 2nd class in a discipline that has statistics
        or quantitative techniques as one of the subjects pursued.
      </h3><br>

       <p><u>Retention and Graduation:</u></p>
      <h3>
        Students admitted into this programme must maintain minimum 
        GPA of 2.0 for each semester to be retained on the programme.
         Only students who make a minimum CGPA of 2.5 and submit a 
         dissertation shall qualify for graduation.
      </h3><br>

        <p><u>Graduation:</u></p>
      <h3>
       Individuals who wish to be part of this programme
        would have to purchase admission forms from designated
        centres for the sale of University of Cape Coast admissions forms.
      </h3>
